@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  min-height: 100%;
}
body {
  background: linear-gradient(30deg, #000000);
  background-image: linear-gradient(315deg, #000000 0%, #000803 74%);
  background-size: 400% 400%;
  -webkit-animation: gradient 8s ease infinite;
          animation: gradient 8s ease infinite;
  min-height: 100%;
  font-family: 'Comfortaa', cursive;
}

.content-container {
  max-width: 80%;
  margin: 2% auto;
  display: flex;
  justify-content: space-between;
  grid-gap: 10px;
  gap: 10px;
  margin-bottom: 5%;
}
.content {
  display: flex;
  justify-content: space-between;
  -webkit-padding-before: 8rem;
          padding-block-start: 8rem;
}
h2 {
  font-size: 3.5rem;
}
.intro-text {
  flex: 0.5 1;
  font-size: 1.4rem;
  color: white;
  opacity: 0.9;
}
.button-container {
  -webkit-margin-before: 2rem;
          margin-block-start: 2rem;
  display: flex;
  flex-direction: column;
  flex: 0.5 1;
  align-items: flex-end;
}
.home-page-button {
  font-weight: 600;
  color: #e7e7e9;
  border-radius: 0.5rem;
  cursor: pointer;
  border: none;
  max-width: 300px;
  padding: 25px 30px;
  font-size: 1rem;
  background-image: linear-gradient(315deg, #000703 50%, #000703 74%);
  -webkit-animation: gradient 8s ease infinite;
          animation: gradient 8s ease infinite;
  box-shadow: -4px 2px 24px 13px #000000;
  font-family: 'Comfortaa', cursive;
  text-decoration: none;
}

.follow-button {
  margin-top: 10px;
}

.home-page-button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  background-color: rgba(145, 207, 1, 0.856);
  transition: all 0.05s ease-in;
}
.projects-container {
  border-radius: 1rem;
  max-width: 90%;
  margin: 0 auto 4%;
}
.other {
  padding: 10px 40px;
  color: #e7e7e9;
  font-size: 1.4rem;
}
.other-projects-title {
  color: white;
  font-weight: bold;
  font-size: 1.7rem;
  width: 10px;
}
.other-projects-text {
  opacity: 0.8;
}

.card-container {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.card {
  background-image: linear-gradient(315deg, #000703 50%, #000703 74%);
  -webkit-animation: gradient 8s ease infinite;
          animation: gradient 8s ease infinite;
  box-shadow: -4px 2px 24px 13px #000000;
  border: 2px solid rgba(0, 7, 3, 0.5);
}
.card:hover {
  transition: all 0.3s ease-in;
  border: 2px solid rgba(21, 138, 72, 0.2);
}
.title {
  font-size: 22px;
  color: white;
  margin: 0;
  flex: 0.5 1;
}
.project-img-todo,
.project-img-movie,
.project-img-data {
  border-radius: 5px;
  opacity: 0.6;
}
.card-text {
  color: white;
  opacity: 0.6;
}
#try {
  color: white;
  opacity: 0.8;
  font-size: 1.2rem;
}
#try:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  background-color: rgba(145, 207, 1, 0.856);
  transition: all 0.1s ease-in;
}
@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

ul {
  list-style: none;
  padding: 0;
}
.tagline {
  margin: 0;
}
.name {
  margin: 0.3em 0;
}
.links {
  display: flex;
  justify-content: space-between;
}
.repo,
.wordpressLink {
  color: white;
  opacity: 0.6;
  text-decoration: none;
}
.repo {
  opacity: 1;
  max-height: 80px;
}
.blog:hover,
.repo:hover {
  color: rgba(145, 207, 1, 1);
}
.description {
  display: flex;
  justify-content: space-between;
}
.tech {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  align-items: center;
}
.blog-text {
  color: rgba(255, 255, 255, 0.6);
}

.ball {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  pointer-events: none;
  mix-blend-mode: difference;
  opacity: 1;
}
.blog {
  font-weight: bold;
  color: white;
  opacity: 1;
  text-decoration: none;
}
.link-container {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  box-sizing: border-box;
}
.blog-link {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
}
.blog-link:hover {
  background-color: rgba(145, 207, 1, 0.856);
  transition: all 0.1s ease-in;
  padding: 0 10px;
  box-sizing: border-box;
  text-align: center;
  vertical-align: center;
  border-radius: 4px;
}
.about-title {
  color: white;
  margin-left: 15%;
  margin-bottom: 5%;
  font-size: 3rem;
}

.back-arrow {
  margin: 1rem;
  padding: 1rem;
  background-color: #4b4b4d;
  width: 1.4rem;
  height: 1.4rem;
  border-radius: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.5;
}
.back-arrow:hover,
.back-arrow-icon:hover {
  opacity: 1;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: 0.2s ease-in;
  color: rgba(145, 207, 1, 0.856);
}
.about-content {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 20rem;
  gap: 20rem;
  -webkit-margin-before: 2rem;
          margin-block-start: 2rem;
  max-width: 75rem;
  margin: 0 auto;
  padding: 0 1.5rem 10rem;
}
.text-area {
  grid-column: 1 / 5;
}
.about-text-1,
.about-text-2,
.about-text-3 {
  color: white;
  font-weight: 400;
  font-size: 20px;
  line-height: 180%;
  margin: 0.5em 2rem;
  max-width: 42.5rem;
}
.about-text-3 {
  padding-bottom: 80px;
}
.contact-section {
  display: flex;
  justify-content: space-around;
  transition: all 0.5s ease-in-out;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  flex-wrap: wrap;
}
@-webkit-keyframes fade-in {
  0% {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
  }
}
@keyframes fade-in {
  0% {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
  }
}
.contact-email {
  color: white;
  font-weight: 400;
  font-size: 25px;
}
.contact-twitter {
  color: white;
  font-weight: 500;
  font-size: 25px;
}
.contact-twitter-link {
  text-decoration: none;
  color: white;
  padding-left: 2px;
}
.contact-twitter-link:hover {
  transition: 0.3s ease-in-out;
  color: rgba(145, 207, 1, 0.856);
}
.contact-button {
  font-size: 1.4rem;
  color: #e7e7e9;
  background-image: linear-gradient(315deg, #000703 50%, #000703 74%);
  -webkit-animation: gradient 8s ease infinite;
          animation: gradient 8s ease infinite;
  box-shadow: -4px 2px 24px 12px #000000;
  height: 3.5rem;
  border-radius: 0.5rem;
  width: 100%;
  cursor: pointer;
  border: none;
  -webkit-animation: fade-in 0.9s ease forwards;
          animation: fade-in 0.9s ease forwards;
  margin-top: 10%;
}
.contact-button:hover {
  background-color: rgba(145, 207, 1, 0.856);
  transition: 0.1s ease-in;
}

.setup-section {
  background-image: linear-gradient(315deg, #000703 50%, #000703 74%);
  -webkit-animation: gradient 8s ease infinite;
          animation: gradient 8s ease infinite;
  box-shadow: -4px 2px 24px 13px #000000;
  border-radius: 0.5rem;
  padding: 0.8rem;
  color: white;
  min-width: 200px;
}
.pc-pic {
  margin-top: 1px;
  border-radius: 0.4rem;
  width: 100%;
}
.pc-text {
  padding: 0 1rem;
  text-align: center;
  margin-top: 2px;
  font-size: 1.1rem;
}
.setup-head-text {
  font-size: 1.5rem;
  text-align: center;
}
.software-list {
  padding-inline: 11px;
}
.setup-list-title {
  font-weight: 700;
  padding-bottom: 0.5rem;
  list-style: none;
}
.setup-text {
  opacity: 0.7;
}

